<script>
  import { onMount } from "svelte";

  onMount(async () => {
    // shim layer with setTimeout fallback
    window.requestAnimFrame = (function() {
      return (
        window.requestAnimationFrame ||
        window.webkitRequestAnimationFrame ||
        window.mozRequestAnimationFrame ||
        window.oRequestAnimationFrame ||
        window.msRequestAnimationFrame ||
        function(callback) {
          window.setTimeout(callback, 1000 / 60);
        }
      );
    })();

    (function(win, d) {
      // if (window.innerWidth < 768) {
      //   return;
      // }

      var $ = d.querySelector.bind(d);

      var blob1 = $("#blob-1");
      var blob2 = $("#blob-2");
      var blob3 = $("#blob-3");
      var blob4 = $("#blob-4");

      var ticking = false;
      var lastScrollY = 0;

      function onResize() {
        updateElements(win.pageYOffset);
      }

      function onScroll(evt) {
        if (!ticking) {
          ticking = true;
          requestAnimFrame(updateElements);
          lastScrollY = win.pageYOffset;
        }
      }

      function updateElements() {
        var relativeY = lastScrollY / 2000;
        var xmultiplier = window.innerWidth / 960;
        // red gradinet
        prefix(
          blob1.style,
          "Transform",
          "translate3d(" +
            600 * xmultiplier +
            "px," +
            pos(900, -1400, relativeY, 0) +
            "px, 0)"
        );
        // green gradinet
        prefix(
          blob2.style,
          "Transform",
          "translate3d(" +
            200 * xmultiplier +
            "px," +
            pos(504, -1540, relativeY, 0) +
            "px, 0)"
        );

        // big  blue
        prefix(
          blob4.style,
          "Transform",
          "translate3d(" +
            -48 * xmultiplier +
            "px," +
            pos(704, -600, relativeY, 0) +
            "px, 0)"
        );

        // big  red
        prefix(
          blob3.style,
          "Transform",
          "translate3d(" +
            500 * xmultiplier +
            "px," +
            pos(-300, -500, relativeY, 0) +
            "px, 0)"
        );

        ticking = false;
      }

      function pos(base, range, relY, offset) {
        return base + limit(0, 1, relY - offset) * range;
      }

      function prefix(obj, prop, value) {
        var prefs = ["webkit", "Moz", "o", "ms"];
        for (var pref in prefs) {
          obj[prefs[pref] + prop] = value;
        }
      }

      function limit(min, max, value) {
        return Math.max(min, Math.min(max, value));
      }

      (function() {
        updateElements(win.pageYOffset);

        blob1.classList.add("force-show");
        blob2.classList.add("force-show");
        blob3.classList.add("force-show");
        blob4.classList.add("force-show");
      })();

      win.addEventListener("resize", onResize, false);
      win.addEventListener("scroll", onScroll, false);
    })(window, document);

    // start recaptcha
    grecaptcha.ready(function() {
      grecaptcha
        .execute("6LcglZEUAAAAADpY_z6HlGSY-uJSqtMO-y2dQ83D", {
          action: "homepage"
        })
        .then(function(token) {
          window.gRecaptchaResponse = token;
        });
    });

    window.submitToAPI = function submitToAPI(e) {
      let URL =
        "https://mwwrpzj209.execute-api.us-east-1.amazonaws.com/Prod/contact-us";

      const emailInput = document.querySelector("#email-input");
      const nameInput = document.querySelector("#name-input");
      const phoneInput = document.querySelector("#phone-input");
      const descInput = document.querySelector("#desc-input");

      const emailButton = document.querySelector("#email-button");

      const formContainer = document.querySelector("#form-container");

      if (emailInput.value == "") {
        alert("Please enter your email id");
        return;
      }

      let reeamil = /^([\w-\.]+@([\w-]+\.)+[\w-]{2,6})?$/;
      if (!reeamil.test(emailInput.value)) {
        alert("Please enter valid email address");
        return;
      }

      let email = emailInput.value;
      let name = nameInput.value;
      let phone = phoneInput.value;
      let desc = descInput.value;

      emailInput.disabled = true;
      nameInput.disabled = true;
      phoneInput.disabled = true;
      descInput.disabled = true;
      emailButton.disabled = true;

      let data = {
        email: email,
        name: name,
        phone: phone,
        desc: desc,
        gRecaptchaResponse: window.gRecaptchaResponse
      };

      fetch(URL, {
        method: "POST",
        body: JSON.stringify(data),
        mode: "cors",
        headers: {
          "Content-Type": "application/json; charset=utf-8"
          // "Content-Type": "application/x-www-form-urlencoded",
        }
      }).then(res => {
        formContainer.innerHTML = `<div  class="mx-auto py-48">Thank you for your message! <br> We will get back to you as soon as possible</div>`;
      });
    };
  });

  import Nav from "../components/NavLightColors.svelte";
  import FooterContact from "../components/FooterContact.svelte";

  function firstMonToFriNextMonth(year, month) {
    var d = new Date();
    d = new Date(
      Number(year) || d.getFullYear(),
      Number(month) || d.getMonth() + 1,
      1
    );
    d.getDay() % 6 ? d : d.setDate((2 + d.getDay()) % 5);
    return d;
  }
  let weekday = new Array(7);
  weekday[0] = "Sunday";
  weekday[1] = "Monday";
  weekday[2] = "Tuesday";
  weekday[3] = "Wednesday";
  weekday[4] = "Thursday";
  weekday[5] = "Friday";
  weekday[6] = "Saturday";

  let month = new Array();
  month[0] = "January";
  month[1] = "February";
  month[2] = "March";
  month[3] = "April";
  month[4] = "May";
  month[5] = "June";
  month[6] = "July";
  month[7] = "August";
  month[8] = "September";
  month[9] = "October";
  month[10] = "November";
  month[11] = "December";

  function nth(d) {
    if (d > 3 && d < 21) return "th";
    switch (d % 10) {
      case 1:
        return d + "st";
      case 2:
        return d + "nd";
      case 3:
        return d + "rd";
      default:
        return d + "th";
    }
  }
  let date = firstMonToFriNextMonth();

  let nextslot =
    month[date.getMonth()] +
    ", " +
    weekday[date.getDay()] +
    " " +
    nth(date.getDate());
</script>

<style>
  #blob-1,
  #blob-2,
  #blob-3,
  #blob-4 {
    position: absolute;
    display: none;
  }

  #blob-container {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 1500px;
    overflow: hidden;
    pointer-events: none;
  }
  .force-show {
    display: block !important;
  }
</style>

<svelte:head>
  <title>Lagarsoft - Contact</title>
</svelte:head>
<Nav />
<container
  class="bg-lgsftDarkBlue flex items-center justify-between flex-wrap py-8 ">
  <container class="z-20 mx-auto text-white text-center ">
    <h2
      class="text-2xl sm:inline-block sm:text-3xl md:text-4xl lg:text-5xl
      sm:w-1/2 lg:w-4/5 xl:w-6/7 w-full text-left z-30 font-hairline
      tracking-wider mb-8">
      Contact
    </h2>
    <container class="flex sm:w-1/2 lg:w-4/5 xl:w-6/7 w-full mx-auto ">
      <container class="w-2/5">
        <p class="text-left z-30 font-hairline tracking-wide leading-tight">
          Want to talk about software development, workshops, or crazy MVP
          ideas? Bring it on!
          <br />
          <br />
          Not into forms? Drop us a note via email:
          <span class=" font-normal underline">info@lagarsoft.com</span>
        </p>
      </container>
      <container class="w-3/5 min-h-full" id="form-container">
        <form class=" " action="javascript:;" onsubmit="submitToAPI(this)">
          <div class="mb-4">
            <input
              class=" appearance-none border w-full py-2 px-3 text-gray-700
              leading-tight focus:outline-none focus:shadow-outline"
              id="name-input"
              type="text"
              placeholder="n a m e"
              autocomplete="off" />
          </div>
          <div class="mb-4">
            <input
              class="appearance-none border w-full py-2 px-3 text-gray-700
              leading-tight focus:outline-none focus:shadow-outline"
              id="email-input"
              type="text"
              placeholder="e m a i l"
              autocomplete="off" />
          </div>
          <div class="mb-4">
            <input
              class=" appearance-none border w-full py-2 px-3 text-gray-700
              leading-tight focus:outline-none focus:shadow-outline"
              id="phone-input"
              type="text"
              placeholder="p h o n e"
              autocomplete="off" />
          </div>
          <div class="mb-4">
            <textarea
              class=" appearance-none border w-full py-2 px-3 text-gray-700
              leading-tight focus:outline-none focus:shadow-outline"
              id="desc-input"
              type="text"
              placeholder="c o m m e n t s"
              autocomplete="off" />
          </div>
          <div class="flex items-center justify-between">
            <button
              id="email-button"
              class="block bg-lgsftRed hover:bg-lgsftRedDark text-white pb-2
              pt-1 pl-4 pr-16 rounded-full tracking-wider my-8 ml-auto"
              type="submit">
              <span class="align-text-top leading-relaxed font-hairline ">
                send
              </span>
            </button>
          </div>
        </form>
        <h2
          class="text-2xl sm:inline-block sm:text-3xl md:text-4xl lg:text-5xl
          text-left font-hairline tracking-wider mb-2 w-full">
          Meeting
        </h2>
        <container class="flex w-1/2">
          <p class="text-left z-30 font-hairline tracking-wide leading-tight">
            Due to our focus on quality and the attention our customers deserve,
            we only take up to 2 new projects each month.
            <br />
            <br />
            Save your slot for the next batch and bootstrap your idea.
            <br />
            (next slot starts on:
            <span class=" font-normal">{nextslot}</span>
            )
            <button
              class="block bg-lgsftRed hover:bg-lgsftRedDark text-white pb-2
              pt-1 pl-4 pr-16 rounded-full tracking-wider my-8 mr-auto"
              onclick="window.location='https://calendly.com/lagarsoft/30min';">
              <span class="align-text-top leading-relaxed font-hairline ">
                schedule
              </span>
            </button>
          </p>

        </container>
      </container>
    </container>
  </container>
  <div id="blob-container">
    <img class="z-20 w-1/4" alt="" id="blob-1" src="img/vector7.svg" />
    <img class="z-20 w-1/7" alt="" id="blob-2" src="img/vector1.svg" />
    <div
      class="z-10"
      alt=""
      id="blob-3"
      style="background-color: rgb(231, 70, 44); border-radius: 50%; width: 60%;
      padding-top:60%;" />
    <div
      class="z-10"
      alt=""
      id="blob-4"
      style="background-color: #49AA9B; border-radius: 50%; width: 40%;
      padding-top:40%;" />
    <div class="force-show" />

  </div>
</container>

<FooterContact />
